import React from 'react';

const AjaxLoader = () => {
  return (
    <div id="ajax-loader">
      <div className="bounce bounce--1"></div>
      <div className="bounce bounce--2"></div>
      <div className="bounce bounce--3"></div>
    </div>
  );
};

export default AjaxLoader;


